* {
  padding: 0px;
  font-family: "Myriad Pro";
  $primary: "#232323" !important;

  box-sizing: border-box;

  body {
    padding-right: 0 !important;
    overflow: auto !important;
  }

  ul {
    margin: 0;
    padding: 0;
    list-style: none;
    li {
      margin: 0;
      padding: 0;
    }
  }

  p {
    margin: 0;
  }

  h1, h2, h3, h4, h5, h6 {
    margin: 0;
    padding: 0;
  }
}